import {store} from '../../store/store.mjs';

export const singUp = {
    delimiters: ['#{', '}'],
    props: {
        status: false
    },
    data () {
        return {
            email: '',
            password: '',
            confirmPassword: '',
            username: '',
            errorMsg: ''
        }
    },
    template: `
                <div class="singup" v-show="status">

                    <div class="singup-wrapper">

                        <i class="fas fa-times" @click="hide"></i>

                        <h3>Ë®ªÂÜä</h3>
                        
                        <form @submit.prevent="singUp">

                            <input type="email" placeholder="ÈõªÂ≠êÈÉµ‰ª∂" v-model="email" required>
                            <input type="text" placeholder="Áî®Êà∂Âêç" v-model="username" required>
                            <input type="password" placeholder="ÂØÜÁ¢º" v-model="password" required>
                            <input type="password" placeholder="Á¢∫Ë™çÂØÜÁ¢º" v-model="confirmPassword" required>
                            <p class="error" v-show="errorMsg">#{errorMsg}</p>

                            <button  class="btn-normal">Ë®ªÂÜä</button>
                        </form>

                        <h4>Â∑≤Á∂ìÊìÅÊúâÂ∏≥Êà∂? <a href="#" @click="showLogin">ÁôªÂÖ•</a></h4>

                    </div>

                </div>
    `,
    methods: {
        singUp () {

            this.errorMsg = '';

            if (this.password !== this.confirmPassword) {
                this.errorMsg = 'ÂØÜÁ¢º‰∏çÂåπÈÖçÔºåË´ãÈáçÊñ∞Ëº∏ÂÖ•';
            }
            
            // post singup api
            this._empty();

        },
        showLogin () {
            this.hide();
            store.state.showLogin = true;
        },
        hide () {
            this._empty();
            store.state.showSingUP = false;
        },
        _empty () {
            this.email = '';
            this.username = '';
            this.password = '';
            this.confirmPassword = '';
        }
    }
}